/* UserProfile.css */

.user-profile {
    padding: 1rem;
    background-color: #f9f9f9;
  }
  
  .user-profile h1 {
    font-size: 1.5rem;
    font-weight: bold;
    color: #333;
  }
  
  .user-profile .profile-photo {
    border-radius: 50%;
    border: 2px solid #ddd;
    max-width: 150px;
    height: auto;
    margin-bottom: 1rem;
  }
  
  .user-profile .cover-photo {
    border-radius: 0.5rem;
    width: 100%;
    height: auto;
  }
  
  .user-profile p {
    font-size: 1rem;
    color: #666;
    margin-top: 0.5rem;
  }
  
  .user-profile .qr-code {
    margin-top: 1rem;
    max-width: 200px;
    height: auto;
  }
  